<template>
  <div id="timeline-component">
    <div class="top-container">
      <div class="brand">
        <img
          :src="formatImageUrl(company.buyer.logo)"
          :alt="company.buyer.name"
        />
      </div>
      <div class="brand">
        <img :src="formatImageUrl(company.supplier.logo)" alt="" />
      </div>
    </div>

    <div class="main-container">
      <div
        v-for="stage in stages"
        :key="stage.id"
        :class="{
          'dot-solid': true,
          right: stage.side === 2,
          left: stage.side === 1
        }"
        @click.stop="onDotClick(stage)"
      >
        <div class="content primary-container">
          <p class="time">{{ dayjs(stage.created_at).format("YYYY.MM.DD") }}</p>
          <p class="name">{{ stage.name }}</p>
        </div>
        <!--<div v-else class="content secondary-container">-->
        <!--<p class="name">{{ item.name }}</p>-->
        <!--</div>-->
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType } from "vue";
import dayjs from "dayjs";
import { ProjectCompany, ProjectStage } from "@/typings/project";
import { formatImageUrl } from "@/utils";

const initCompany = () => ({
  supplier: { id: -1, name: "", logo: "" },
  buyer: { id: -1, name: "", logo: "" }
});

const Component = defineComponent({
  name: "Timeline",
  props: {
    company: {
      type: Object as PropType<{
        supplier: ProjectCompany;
        buyer: ProjectCompany;
      }>,
      default: initCompany()
    },
    stages: {
      type: Array as PropType<ProjectStage[]>,
      default: () => []
    }
  },
  setup(_, { emit }) {
    const onDotClick = (data: ProjectStage) => {
      emit("click", data);
    };

    return {
      onDotClick,

      dayjs,
      formatImageUrl
    };
  }
});

export default Component;
</script>

