<template>
  <div id="project-detail-component">
    <div class="top-container">
      <h1 v-if="projectData">{{ projectData.name }}</h1>
      <div class="right-container">
        <el-button
          v-permission="'project_members_update'"
          type="info"
          @click="onUsersClick"
        >
          Users
        </el-button>
        <el-button
          v-permission="'project_status_update'"
          type="success"
          @click="onAddStageClick"
          >Add Stage</el-button
        >
        <el-button type="success" @click="onInformClick">Inform</el-button>
        <el-button
          v-permission="'project_status_update'"
          type="success"
          @click="onEndClick"
        >
          End
        </el-button>
      </div>
    </div>

    <div class="main-container" v-if="projectData">
      <el-row :gutter="40" style="margin: 0;">
        <el-col :span="8" style="padding-left: 0;">
          <Timeline
            :company="projectData.company"
            :stages="projectData.stages"
            :fabric="fabricData"
            @click="onStageClick"
          ></Timeline>
        </el-col>

        <el-col :span="16" style="padding-right: 0;">
          <template v-if="stageData">
            <div class="card">
              <h3 class="title">Stage</h3>
              <p class="content">{{ stageData.name }}</p>
            </div>
            <div class="card">
              <h3 class="title">Time</h3>
              <p class="content">
                {{ dayjs(stageData.created_at).format("YYYY.MM.DD") }}
              </p>
            </div>
            <div class="card">
              <h3 class="title">Description</h3>
              <p class="content">{{ stageData.description || "Empty..." }}</p>
              <div class="report-container">
                <div
                  v-for="file in stageData.stageattachements"
                  :key="file.id"
                  class="report-item"
                  @click="onAttachmentDownload(file)"
                >
                  <font-awesome-icon
                    class="icon"
                    :icon="['far', 'file']"
                  ></font-awesome-icon>
                  <p>{{ file.name }}.{{ file.file_type }}</p>
                </div>
              </div>
            </div>
            <div class="card">
              <h3 class="title">Discussion</h3>
              <div class="input-discussion">
                <font-awesome-icon
                  class="user"
                  :icon="['far', 'user-circle']"
                ></font-awesome-icon>
                <el-input
                  :aria-disabled="!isOpenInputDiscussion"
                  placeholder="Write a comment..."
                  v-model="discussionValue"
                  @keydown.enter="onDiscuss"
                >
                  <template #append>
                    <el-button
                      icon="el-icon-position"
                      @click="onDiscuss"
                    ></el-button>
                  </template>
                </el-input>
                <div class="upload-wrapper">
                  <font-awesome-icon
                    class="paperclip"
                    :icon="['fas', 'paperclip']"
                    @click="onUploadClick"
                  ></font-awesome-icon>
                  <div v-if="filesStore" class="count">
                    {{ filesStore.length }}
                  </div>
                </div>
              </div>
              <div
                v-for="item in stageData.discussions"
                :key="item"
                class="chat-item"
              >
                <div class="avatar-container">
                  <font-awesome-icon
                    class="icon"
                    :icon="['far', 'user-circle']"
                  ></font-awesome-icon>
                  <div class="name">{{ item.creator }}</div>
                </div>
                <div class="content">
                  <p class="message">{{ item.message }}</p>
                  <div class="files-wrapper">
                    <div
                      v-for="file in item.attachements"
                      :key="file.id"
                      class="file-item"
                      @click="onAttachmentDownload(file)"
                    >
                      <font-awesome-icon
                        class="icon"
                        :icon="['far', 'file']"
                      ></font-awesome-icon>
                      <p>{{ file.name }}.{{ file.file_type }}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </template>
        </el-col>
      </el-row>
    </div>

    <CreateStageDialog
      ref="createStageDialog"
      @load="onReload"
    ></CreateStageDialog>
    <InformDialog ref="informDialog"></InformDialog>
    <EndDialog ref="endDialog"></EndDialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, onMounted, toRefs } from "vue";
import { useRouter } from "vue-router";
import dayjs from "dayjs";

import Timeline from "./Timeline.vue";
import CreateStageDialog from "./CreateStageDialog.vue";
import InformDialog from "./InformDialog.vue";
import EndDialog from "./EndDialog.vue";

import { formatImageUrl, loading } from "@/utils";
import {
  getProject,
  getProjectStage,
  postCreateDiscussion
} from "@/apis/project";
import {
  Attachment,
  DiscussionInput,
  Project,
  ProjectStage,
  Stage
} from "@/typings/project";
import { FabricDetailResponse } from "@/typings/fabric";
import { getFabric } from "@/apis/fabric";
import { iCreateDialog } from "@/typings/component";

const Component = defineComponent({
  name: "ProjectDetail",
  components: {
    Timeline,
    CreateStageDialog,
    InformDialog,
    EndDialog
  },
  props: {
    id: {
      type: String,
      default: ""
    }
  },
  setup(props) {
    const router = useRouter();
    const { id } = toRefs(props);
    const createStageDialog = ref<iCreateDialog | null>(null);
    const informDialog = ref<iCreateDialog | null>(null);
    const endDialog = ref<iCreateDialog | null>(null);

    const projectData = ref<Project | null>(null);
    const projectStage = ref<ProjectStage | null>(null);
    const fabricData = ref<FabricDetailResponse | null>(null);
    const stageData = ref<Stage | null>(null);
    const discussionValue = ref("");
    const isOpenInputDiscussion = ref(true);
    const filesStore = ref<FileList | null>(null);

    const fetchStageDetail = async (stage: ProjectStage) => {
      projectStage.value = stage;
      const { data } = await getProjectStage(stage.id);
      stageData.value = data;
    };

    const init = async (stageIndex = 0) => {
      const loadingInstance = loading();
      console.log("id:", id.value);
      try {
        const { data } = await getProject(parseInt(id.value, 10));
        projectData.value = data;
        if (data.stages.length) {
          await fetchStageDetail(data.stages[stageIndex]);
        }
        if (data.start_fabric) {
          const { data: fabricResponse } = await getFabric(data.start_fabric);
          fabricData.value = fabricResponse;
        }
      } catch (_) {
        await router.replace({ name: "Error" });
      } finally {
        loadingInstance.close();
      }
    };

    onMounted(init);

    const onUsersClick = () => {
      console.log("users");
    };
    const onAddStageClick = () => {
      if (!createStageDialog.value || !projectData.value) return;
      createStageDialog.value.open(projectData.value);
    };
    const onInformClick = () => {
      if (!informDialog.value) return;
      informDialog.value.open();
    };
    const onEndClick = () => {
      if (!endDialog.value) return;
      endDialog.value.open();
    };
    const onStageClick = async (data: ProjectStage) => {
      await fetchStageDetail(data);
    };
    const onDiscuss = async () => {
      if (
        !projectStage.value ||
        (!discussionValue.value && !filesStore.value)
      ) {
        return;
      }

      isOpenInputDiscussion.value = false;
      try {
        const params: DiscussionInput = {
          message: discussionValue.value,
          stage: projectStage.value.id
        };
        if (filesStore.value) params.files = filesStore.value;

        discussionValue.value = "";
        await postCreateDiscussion(params);
        await fetchStageDetail(projectStage.value);
      } finally {
        isOpenInputDiscussion.value = true;
      }
    };
    const onUploadClick = () => {
      if (!projectStage.value) return;

      const element = document.createElement("input");
      element.type = "file";
      element.accept = "image/*";
      element.multiple = true;
      element.addEventListener("change", async () => {
        if (!projectStage.value) return;

        if (element.files) {
          filesStore.value = element.files;
        }
      });
      element.click();
    };

    const onAttachmentDownload = (data: Attachment) => {
      window.open(formatImageUrl(data.file));
    };

    const onReload = async () => {
      if (!projectData.value) return;
      await init(projectData.value.stages.length);
    };

    return {
      createStageDialog,
      informDialog,
      endDialog,
      projectData,
      fabricData,
      stageData,
      discussionValue,
      isOpenInputDiscussion,
      filesStore,

      onUsersClick,
      onAddStageClick,
      onInformClick,
      onEndClick,
      onStageClick,
      onDiscuss,
      onUploadClick,
      onAttachmentDownload,
      onReload,

      dayjs
    };
  }
});

export default Component;
</script>

