
import { defineComponent, PropType } from "vue";
import dayjs from "dayjs";
import { ProjectCompany, ProjectStage } from "@/typings/project";
import { formatImageUrl } from "@/utils";

const initCompany = () => ({
  supplier: { id: -1, name: "", logo: "" },
  buyer: { id: -1, name: "", logo: "" }
});

const Component = defineComponent({
  name: "Timeline",
  props: {
    company: {
      type: Object as PropType<{
        supplier: ProjectCompany;
        buyer: ProjectCompany;
      }>,
      default: initCompany()
    },
    stages: {
      type: Array as PropType<ProjectStage[]>,
      default: () => []
    }
  },
  setup(_, { emit }) {
    const onDotClick = (data: ProjectStage) => {
      emit("click", data);
    };

    return {
      onDotClick,

      dayjs,
      formatImageUrl
    };
  }
});

export default Component;
