<template>
  <div id="create-stage-dialog-component">
    <el-dialog title="Add Stage" v-model="visible" width="50%" top="45px">
      <div class="input-name-wrapper">
        <el-select
          class="select"
          popper-class="select-popper"
          v-model="form.side"
        >
          <el-option
            v-for="item in sideOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <el-input
          class="input"
          v-model="form.name"
          placeholder="Stage name..."
        ></el-input>
      </div>

      <div class="textarea-wrapper">
        <el-input
          type="textarea"
          placeholder="Stage description..."
          v-model="form.description"
          :rows="10"
        ></el-input>
        <div class="upload-wrapper">
          <font-awesome-icon
            class="paperclip"
            :icon="['fas', 'paperclip']"
            @click="onUploadClick"
          ></font-awesome-icon>
          <div v-if="filesStore" class="count">
            {{ filesStore.length }}
          </div>
        </div>
      </div>

      <div class="bottom-wrapper">
        <font-awesome-icon
          class="alert-icon"
          :icon="['fas', 'info-circle']"
        ></font-awesome-icon>
        <p class="alert-text">Cannot edit after adding stage</p>
        <el-button type="info" @click="onDoneClick">Add Stage</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { Project, StageCreateInputs } from "@/typings/project";
import { OptionItem } from "@/typings/asideFilter";
import { postCreateStage } from "@/apis/project";

const Component = defineComponent({
  name: "CreateStageDialog",
  setup(_, { emit }) {
    const visible = ref(false);
    const sideOptions = ref<OptionItem[]>([]);
    const filesStore = ref<FileList | null>(null);
    const form = reactive<StageCreateInputs>({
      name: "",
      description: "",
      project: -1,
      side: 1
    });

    const open = async (data: Project) => {
      form.project = data.id;
      sideOptions.value = [
        { label: data.company.supplier.name, value: 1, checked: false },
        { label: data.company.buyer.name, value: 2, checked: false }
      ];
      visible.value = true;
    };

    const onUploadClick = () => {
      const element = document.createElement("input");
      element.type = "file";
      element.accept = "image/*";
      element.multiple = true;
      element.addEventListener("change", async () => {
        if (element.files) {
          filesStore.value = element.files;
        }
      });
      element.click();
    };

    const onDoneClick = async () => {
      if (filesStore.value) {
        form.files = filesStore.value;
      }
      await postCreateStage(form);
      emit("load");
      visible.value = false;
    };

    return {
      visible,
      form,
      sideOptions,
      filesStore,

      open,
      onUploadClick,
      onDoneClick
    };
  }
});

export default Component;
</script>

